:root {
  --active-color: #00acdc;
}

body {
  text-transform: uppercase;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

ul, li {
  margin: 0;
  padding: 0;
  list-style: none;
}

a, a:visited {
  text-decoration: none;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  height: 40px;
}

.nav__list {
  display: flex;
}

.nav__link {
  color: #000;
  height: 60px;
  margin-right: 20px;
  font-size: 12px;
  line-height: 60px;
  display: flex;
  position: relative;
}

.nav__link-last {
  margin-right: 0;
}

.nav__link:hover {
  color: var(--active-color);
}

.is-active {
  color: var(--active-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--active-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
